import React, { Component } from 'react';
import axios from 'axios';
import { Table, Button, Row, Tooltip, OverlayTrigger } from 'react-bootstrap';
import Loader from './Loader';
import { OwnerDeleteDialog } from './OwnerDeleteDialog';

export class Owners extends Component {
  state = {
    loading: false,
    showDeleteDialog: false,
    ownerDelete: '',
    owners: [],
    sortedBy: {
      name: 'last_name',
      direction: 'asc',
    },
  };

  url = '/testforjob/api/owners/';
  tooltipPlace = 'bottom';

  //upArrow = '&#x0225C;';
  upArrow = '\u2191';
  //downArrow = '&#x0225C;';
  downArrow = '\u2193';

  componentDidMount() {
    this.getOwners();
  }

  componentDidUpdate(prevProps, prevState) {
    if (
      prevState.sortedBy.name !== this.state.sortedBy.name ||
      prevState.sortedBy.direction !== this.state.sortedBy.direction
    ) {
      this.getOwners();
    }
  }

  getOwners = () => {
    this.setState({ loading: true });
    axios
      .post(this.url, { sorted_by: this.state.sortedBy })
      .then((res) => {
        console.log('getOwners', res.data);
        this.setState({ owners: res.data });
      })
      .catch((err) => console.log('getOwners', err.response.data))
      .finally(() => this.setState({ loading: false }));
  };

  btnSortClick = (e) => {
    const sorted_name = e.target.id;
    //console.log('btnSortClick e', e);

    if (this.state.sortedBy.name !== sorted_name) {
      const sortedBy = {
        name: sorted_name,
        direction: 'desc',
      };
      //console.log('btnSortClick sortedBy', sortedBy);
      this.setState({
        sortedBy: sortedBy,
      });
    } else {
      const direction = this.state.sortedBy.direction === 'desc' ? 'asc' : 'desc';
      //console.log('btnSortClick direction', direction);

      const sortedBy = {
        ...this.state.sortedBy,
        direction: direction,
      };
      //console.log('btnSortClick', sortedBy);

      this.setState({ sortedBy: sortedBy });
    }
  };

  get arrow() {
    //console.log('arrow', this.state.sortedBy.direction);
    return this.state.sortedBy.direction === 'asc' ? this.upArrow : this.downArrow;
  }

  getOwner = (id) => {
    return this.state.owners.filter((o) => +o.id === +id)[0];
  };

  btnDelClick = (e) => {
    //console.log('btnDelClick', e);

    const owner = this.getOwner(e.target.value);
    console.log('btnDelClick', owner);
    this.setState({
      showDeleteDialog: true,
      ownerDelete: owner,
    });
  };
  btnEditClick = (e) => {
    axios
      .post(this.url, { btn_edit: '', owner_pk: e.target.value })
      .then((res) => {
        if (res.data.redirect) {
          window.location.href = res.data['redirect'];
        }
      })
      .catch((err) => console.log('btnEditClick', err.response.data));
  };
  btnAddClick = (e) => {
    axios
      .post(this.url, { btn_add: '' })
      .then((res) => {
        if (res.data.redirect) {
          window.location.href = res.data['redirect'];
        }
      })
      .catch((err) => console.log('btnAddClick', err.response.data));
  };

  ownerDelete = (confirm) => {
    this.setState({ showDeleteDialog: false });
    console.log('ownerDelete', confirm);

    if (confirm === 'true') {
      this.setState({ loading: true });
      axios
        .post(this.url, {
          sorted_by: this.state.sortedBy,
          btn_del: '',
          owner_pk: this.state.ownerDelete.id,
        })
        .then((res) => {
          console.log('delBtnClick', res.data);
          this.setState({ owners: res.data });
        })
        .catch((err) => console.log('delBtnClick', err.response.data))
        .finally(() => this.setState({ loading: false }));
    }
  };

  render() {
    return (
      <div>
        {this.state.loading && <Loader />}
        {this.state.showDeleteDialog && (
          <OwnerDeleteDialog params={this.state} ownerDelete={this.ownerDelete} />
        )}

        <Table striped bordered hover>
          <thead>
            <tr>
              <OverlayTrigger
                key={1}
                placement={this.tooltipPlace}
                overlay={<Tooltip id={`tooltip-1`}>Нажмите для сортировки</Tooltip>}
              >
                <th id="last_name" onClick={this.btnSortClick}>
                  <Row id="last_name">
                    {this.state.sortedBy.name === 'last_name' && (
                      <div id="last_name" className="ml-2">
                        {this.arrow}
                      </div>
                    )}{' '}
                    <div id="last_name" className="ml-2">
                      Фамилия
                    </div>
                  </Row>
                </th>
              </OverlayTrigger>
              <OverlayTrigger
                key={2}
                placement={this.tooltipPlace}
                overlay={<Tooltip id={`tooltip-2`}>Нажмите для сортировки</Tooltip>}
              >
                <th id="name" onClick={this.btnSortClick}>
                  <Row id="name">
                    {this.state.sortedBy.name === 'name' && (
                      <div id="name" className="ml-2">
                        {this.arrow}
                      </div>
                    )}{' '}
                    <div id="name" className="ml-2">
                      Имя
                    </div>
                  </Row>
                </th>
              </OverlayTrigger>
              <th id="patronymic">Отчество</th>
              <OverlayTrigger
                key={3}
                placement={this.tooltipPlace}
                overlay={<Tooltip id={`tooltip-3`}>Нажмите для сортировки</Tooltip>}
              >
                <th id="gender" onClick={this.btnSortClick}>
                  <Row id="gender">
                    {this.state.sortedBy.name === 'gender' && (
                      <div id="gender" className="ml-2">
                        {this.arrow}
                      </div>
                    )}
                    <div id="gender" className="ml-2">
                      Пол
                    </div>
                  </Row>
                </th>
              </OverlayTrigger>
              <OverlayTrigger
                key={4}
                placement={this.tooltipPlace}
                overlay={<Tooltip id={`tooltip-4`}>Нажмите для сортировки</Tooltip>}
              >
                <th id="age" onClick={this.btnSortClick}>
                  <Row id="age">
                    {this.state.sortedBy.name === 'age' && (
                      <div id="age" className="ml-2">
                        {this.arrow}
                      </div>
                    )}{' '}
                    <div id="age" className="ml-2">
                      Возраст
                    </div>
                  </Row>
                </th>
              </OverlayTrigger>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {this.state.owners.map((o, index) => {
              return (
                <tr key={index}>
                  <td>{o.last_name}</td>
                  <td>{o.name}</td>
                  <td>{o.patronymic}</td>
                  <td>{o.gender === 'f' ? 'Жен' : 'Муж'}</td>
                  <td>{o.age}</td>
                  <td style={{ width: 100 + 'px' }}>
                    <Row>
                      <OverlayTrigger
                        key={5}
                        placement={this.tooltipPlace}
                        overlay={<Tooltip id={`tooltip-5`}>Редактирование</Tooltip>}
                      >
                        <Button
                          value={o.id}
                          variant="primary"
                          className="ml-2"
                          onClick={this.btnEditClick}
                        >
                          {'\u27f6'}
                        </Button>
                      </OverlayTrigger>
                      <OverlayTrigger
                        key={6}
                        placement={this.tooltipPlace}
                        overlay={<Tooltip id={`tooltip-6`}>Удаление</Tooltip>}
                      >
                        <Button
                          value={o.id}
                          variant="danger"
                          className="ml-2"
                          onClick={this.btnDelClick}
                        >
                          x
                        </Button>
                      </OverlayTrigger>
                    </Row>
                  </td>
                </tr>
              );
            })}
          </tbody>
        </Table>
        <OverlayTrigger
          key={7}
          placement={this.tooltipPlace}
          overlay={<Tooltip id={`tooltip-7`}>Добавление нового автовладельца</Tooltip>}
        >
          <Button variant="primary" className="col" onClick={this.btnAddClick}>
            +
          </Button>
        </OverlayTrigger>
      </div>
    );
  }
}
